.footer {
    border-top: 1px solid var(--iconColor);
    &__top {
        padding: 0 0 35px;
    }
    .container > .flexWrap {
        margin-left: -15px;
        margin-right: -15px;
    }
    &__top-item {
        margin-top: 50px;
        max-width: 25%;
        width: 100%;
    }
    ul {
        margin-top: 23px;
    }
    li {
        display: flex;
        span {
            display: inline-block;
            width: 100%;
            margin-bottom: 5px;
        }
        p {
            margin: 0;
            font-weight: 600;
        }
    }
    li:not(:last-child) {
        margin-bottom: 13px;
    }
    &__title {
        margin: 0;
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        &:not(:first-child) {
            margin-top: 27px;
        }
        &::after {
            content: "";
            display: block;
            background-color: var(--brandColor);
            width: 100%;
            height: 1px;
            margin-top: 13px;
        }
    }
    &__bottom {
        padding: 16px 0;
        .flexRow {
            justify-content: space-between;
            align-items: center;
        }
    }
    &__bottom-text {
        color: var(--DarkWhite);
        p {
            margin: 8px 40px 8px 0;
        }
    }
    &__bottom-socialitem {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 42px;
        height: 42px;
        border-radius: 50%;
        border: 2px solid #2f3233;
        color: #f2f3f4;
        &:not(:first-child) {
            margin-left: 20px;
        }
    }
}
@media(max-width: 1050px) {
    .footer {
        &__top-item {
            max-width: 50%;
            width: 100%;
        }
        &__bottom {
            .container > .flexRow {
                flex-direction: column;
                justify-content: flex-start;
            }
        }
        &__bottom-social {
            margin-top: 30px;
        }
    }
}
@media(max-width: 540px) {
    .footer {
        &__top-item {
            max-width: 100%;
            width: 100%;
        }
        &__bottom-text {
            flex-direction: column;
            p {
                margin-right: 0;
                margin-bottom: 15px;
            }
        }
    }
}